.projects {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.projectsTitle {
  text-align: center;
  font-size: 2.4rem;
  font-weight: 500;
}
.projectImageDiv {
  width: 100%;
}
.projectImage {
  width: 100%;
}
.project {
  margin: 1.2rem;
  box-shadow: 0 0 5px 1px rgb(139, 139, 139);
}
.projectTitle {
  font-size: 2rem;
  text-align: center;
  padding-top: 1rem;
}
.projectText {
  padding: 1rem;
  text-align: center;
  font-size: 1.6rem;
  color: rgb(39, 39, 39);
}